.a {
    height: 100vh;
    display: flex;
    align-items: center;
    background-color:  #1F1D36;
  }
  
  .a-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
  }
  
  .a-right {
    flex: 1;
  }
  h1{
      color: #E9A6A6;
  }

  .a-card.bg {
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #3F3351;
  }
  
  .a-card {
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
  }
  
  .a-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .a-award-img {
    width: 120px;
    height: 120px;
    border-radius: 20px;
  }
  
  .a-title {
    font-weight: 400;
    
  }
  
  .a-sub {
    margin: 20px 0px;
    font-weight: bold;
    color: white;
  }
  
  .a-desc{
      font-weight: 300;
      
      color: white;
  }
  
  
  @media screen and (max-width:480px) {
      .a{
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
      }
  
      .a-left {
        width: 100%;
      }
  
      .a-card {
        height: 30vh;
      }
  
      .a-card.bg {
        display: none;
      }
  
      .a-right{
        padding: 20px;
      }
  }
  @media screen and (max-width:480px) {
    .a{
      flex-direction: column;
      text-align: center;
      margin-top: 50px;
    }

    .a-left {
      width: 100%;
    }

    .a-card {
      height: 30vh;
    }

    .a-card.bg, .a-award {
      display: none;
    }

    .a-right{
      padding: 20px;
    }
}